.App {
  text-align: center;
  font-family: 'Ranchers', cursive;
}

.topTitles {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: 80%;
}

.topTitles h2 {
  transition: 1s;
}

.topTitles h2:hover {
  cursor: pointer;
  transition: 2s;
}

.chosen {
  color: rgb(244, 67, 54);
  font-size: 200%;
  text-decoration: underline;
}
header {
    font-family: 'Ranchers', cursive;
    color: rgb(244, 67, 54);
}
footer {
    background-image: linear-gradient(to bottom right, rgb(244, 67, 54), rgb(255, 255, 255));
    /* position: fixed;
  bottom: 0; */
    width: 100%;
    margin-top: 5%;
    padding: 10px;
}

.ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    top: 0;
    z-index: 1;
    list-style-type: none;
}

.Icon {
    height: 50px;
    width: 50px;
    transition: 1500ms;
}

.Icon:hover {
    border-radius: 50%;
    border: 1px solid aliceblue;
    background-color: aliceblue;
    transition: 1500ms;
    height: 150%;
}

.circle {
    border-radius: 50%;
}

.name {
    font-family: 'Ranchers', cursive;
    -webkit-text-stroke: 0.2px black;
    transition: 1500ms;
    color: black;
}

.name:hover {
    transition: 1500ms;
}
#navbar {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    font-family: 'Ranchers', cursive;
    cursor: pointer;
    z-index: 1;
}

#main-navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    background-image: linear-gradient(to bottom right, rgb(244, 67, 54), rgb(255, 255, 255));
    height: 10%;
    font-size: 200%;
}

#purchase-bar {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-size: calc(1vw + 1vh + 1vmin);
}

.selected {
    font-weight: bold;
    text-decoration: underline;
}

#items-bar {
    padding: 1%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    max-height: 10%;
    font-size: calc(1vw + 1vh + 1vmin);
    background-color: rgb(91, 91, 91);
    color: rgb(223, 32, 102);
}

#error {
    font-size: calc(0.5vw + 0.5vh + 0.5vmin);
}

